import React, { Component } from "react";
import "../upload/index.css";
import ReactJson from "react-json-view";

/*!
 * 显示JsonPage
 */
class JsonPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      json: { key: "value" }
    };
  }

  /*!
   * GET请求
   */
  fetchGET = path => {
    console.log(`http://${window.location.host}` + path);
    return fetch(`http://${window.location.host}` + path, {}).then(res => res.json());
  };

  componentDidMount() {
    this.fetchGET("/json").then(json => this.setState({ json }));
  }

  render() {
    return (
      <div className="logger_con" ref={c => (this.scroll = c)}>
        <ReactJson
          name={false}
          theme="monokai"
          iconStyle="circle"
          collapsed={false}
          displayDataTypes={false}
          src={this.state.json}
          style={{ paddingLeft: 16, backgroundColor: "#ffffff00", paddingTop: 16 }}
        />
      </div>
    );
  }
}

export default JsonPage;
