import React, { Component } from "react";
import "../upload/index.css";
import { logFormate, logCellRenderer } from "../../utils/log";
import { fetchGET } from "../../utils/request";

/*!
 * 显示JsonPage
 */
class JsonPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: []
    };
  }

  componentDidMount() {
    fetchGET("/logHistory").then(res => {
      this.parseString(res.data);
    });
  }

  parseString = res => {
    const list = res.split(/[^\\n](?=👉)/);
    console.log(list);
    let data = [];
    list.map(txt => (data = data.concat(logFormate(txt))));
    console.log(data.length, data);
    this.setState({ data });
  };

  render() {
    return (
      <div className="logger_con" ref={c => (this.scroll = c)}>
        {this.state.data.map(et => {
          return logCellRenderer(et);
        })}
      </div>
    );
  }
}

export default JsonPage;
