import React, { Component } from "react";
import "./index.css";
import { logFormate, logCellRenderer } from "../../utils/log";
import { Upload, Icon, Input, Select } from "antd";
import "antd/lib/input/style/index.css";
import "antd/lib/select/style/index.css";
import "antd/lib/progress/style/index.css";
import "antd/lib/upload/style/index.css";
import "antd/lib/tooltip/style/index.css";
import "antd/lib/icon/style/index.css";
import "antd/lib/message/style/index.css";
import { fetchGET } from "../../utils/request";
const Dragger = Upload.Dragger;
const InputGroup = Input.Group;
const { Option } = Select;

const props = {
  name: "dek",
  multiple: true,
  action: "uploadDek",
  showUploadList: true
};

/*!
 * TODO:
 *   1.优化显示个数
 *   2.替换页面Icon
 */
class Logger extends Component {
  constructor(props) {
    super(props);
    this.list = [];
    this.isBottom = true;
    this.routeType = "Dispatcher";
    this.routeUrd = "";
    this.state = {
      /// 最大日志数
      maxCount: 1000,
      /// 日志筛选器
      filter: "",
      data: [],
      route: [
        "👉PLINE | [分隔]",
        "👉BEGIN | [Router] Router使用.",
        "👉PLINE | [分隔]",
        "👉SINGL | [开始使用]"
      ],
      dek: [
        "👉PLINE | [分隔]",
        "👉BEGIN | [DEK] DEK替换 Start.",
        "👉PLINE | [分隔]",
        "👉SINGL | [开始使用]"
      ],
      logger: [
        "👉PLINE | [分隔]",
        "👉BEGIN | [日志] Start.",
        "👉PLINE | [分隔]",
        "👉SINGL | [日志使用说明]",
        "👉ERROR | [错误] a.Style(表示不可恢复的错误).",
        "👉LWARN | [警告] b.Style(表示可恢复的错误).",
        "👉LINFO | [信息] c.Style(表示非错误的信息).",
        "👉DEBUG | [开发] d.Style(表示数据用于调试).",
        "👉VBOSE | [详细] e.Style(提供所有执行细节).",
        "👉PLINE | [分隔]"
      ],
      row: 0
    };
  }

  /*!
   * 启动WebSocket
   */
  startServer = () => {
    if (!("WebSocket" in window)) return;
    try {
      const ws = new WebSocket("%%WEBSOCKET_URL%%");
      ws.onopen = () => {
        console.log("链接成功");
      };
      ws.onmessage = evt => {
        this.saveData(evt.data);
      };
    } catch (error) {}
  };

  /**
   * 保存数据到存储库
   * @param {String} 日志列表
   */
  saveData = txt => {
    let list = logFormate(txt);
    /// 经过日志过滤器
    // if (this.state.filter.length && !htxt.startWith(`👉${this.state.filter}`)) {
    //   return;
    // }
    list = [...this.list, ...list];
    const start = Math.min(list.count - this.state.maxCount, 0);
    this.list = list.slice(start);
  };

  componentDidMount() {
    this.startServer();
    // 模拟推送数据
    // setInterval(() => {
    //   this.saveData(
    //     `👉DEBUG | [Request] => Params: {\n  "work_type" : 2,\n  "lng" : "116.2982",\n  "lat" : "40.0415",\n  "recommend" : 1,\n  "citydomain" : "bj",\n  "job_type" : 0\n,\n "index":${this.state.data.length}}`
    //   );
    // }, 400);
    // 定时更新数据;
    setInterval(() => {
      this.refreshLogger();
    }, 200);
    if (this.scroll) {
      this.scroll.addEventListener("scroll", () => {
        const { clientHeight, scrollHeight, scrollTop } = this.scroll;
        this.isBottom = clientHeight + scrollTop === scrollHeight;
      });
    }
  }

  componentWillUnmount() {
    if (this.scroll) {
      this.scroll.removeEventListener("scroll", () => {});
    }
  }

  /// 更新UI
  refreshLogger = () => {
    if (
      this.list.length !== this.maxCount &&
      this.list.length === this.state.data.length
    ) {
      return;
    }
    this.setState({ data: this.list ? this.list : [] });
    clearTimeout(this.time);
    this.time = setTimeout(() => {
      if (this.scroll && this.isBottom) {
        this.scroll.scrollTop = this.scroll.scrollHeight;
      }
    }, 10);
  };

  goAction = () => {
    if (!this.routeUrd.length) {
      return;
    }
    fetchGET(`/urd?type=${this.routeType}&urd=${this.routeUrd}`).then(res => {});
  };

  renderRouter = () => {
    return (
      <div className="logger_route">
        <InputGroup compact className="logger_route">
          <Select defaultValue="Dispatcher" saveData={type => (this.routeType = type)}>
            <Option value="Dispatcher">Dispatcher</Option>
            <Option value="Router">Router</Option>
          </Select>
          <Input
            style={{ width: "70%" }}
            suffix={<div onClick={() => this.goAction()}>GO</div>}
            onChange={e => {
              this.routeUrd = e.target.value;
            }}
          />
        </InputGroup>
      </div>
    );
  };

  renderDEK = () => {
    return (
      <div className="logger_dek">
        <Dragger {...props}>
          <p className="ant-upload-drag-icon">
            <Icon type="inbox"></Icon>
          </p>
          <p className="ant-upload-text">点击或将DEK文件拖拽到此区域上传</p>
          <p className="ant-upload-hint">
            支持单个上传，严禁上传公司内部资料及其他违禁文件
          </p>
        </Dragger>
      </div>
    );
  };

  render() {
    return (
      <div className="logger_con" ref={c => (this.scroll = c)}>
        {this.state.route.map(txt => logCellRenderer(logFormate(txt)[0]))}
        {this.renderRouter()}
        {this.state.dek.map(txt => logCellRenderer(logFormate(txt)[0]))}
        {this.renderDEK()}
        {this.state.logger.map(txt => logCellRenderer(logFormate(txt)[0]))}
        {this.state.data.map(et => {
          return logCellRenderer(et);
        })}
      </div>
    );
  }
}

export default Logger;
