import React from "react";
import "../pages/upload/index.css";
import ReactJson from "react-json-view";

//===============================================================
// REQUEST - 日志格式化
//===============================================================

/*!
 * 字符串格式化
 */
export const logFormate = txt => {
  return handleTxt(txt);
};

const handleTxt = txt => {
  const nt = txt.replace('\\"', '"');
  const mt = nt.match(/\{(.|\s)*(?=\})\}/g);
  if (!mt) return [handleString(txt)];
  const re = nt.replace(/\{(.|\s)*(?=\})\}/g, "");
  return [handleString(re), mt[0]];
};

const handleString = str => {
  const group = str.match(/^(?:👉)?(?:(.*?)\s*\|)?(?:\s*\[(.*?)\])?/g);
  if (!group) return { level: "", tag: "", str };
  const level = RegExp.$1;
  const tag = RegExp.$2;
  const ns = str.replace(/^(?:👉)?(?:(.*?)\s*\|)?(?:\s*\[(.*?)\])?\s*(?:=>)?\s*/g, "");
  return { level, tag, ...handleStr(ns) };
};

const handleStr = str => {
  const mt = str.match(/^(\d+|\w|(i|v)+)\.\s*/g);
  if (!mt) return { str };
  const ltag = RegExp.$1;
  const re = str.replace(/^(\d+|\w|(i|v)+)\.\s*/g, "");
  return { ltag, str: re };
};

const getLevelStyle = (level, tag) => {
  const style = {
    paddingRight: 10
  };
  let nlevel = level;
  if (level === "ERROR") {
    style.color = "RGB(225,112,85)";
    nlevel = `📍${level}`;
  } else if (level === "LWARN") {
    style.color = "RGB(253,203,110)";
    nlevel = `✏️${level}`;
  } else if (level === "LINFO") {
    style.color = "RGB(46,204,113)";
    nlevel = `🌟${level}`;
  } else if (level === "DEBUG") {
    style.color = "RGB(9,132,227)";
    nlevel = `🖌${level}`;
  } else if (level === "VBOSE") {
    style.color = "RGB(0,206,201)";
    nlevel = `💫${level}`;
  } else if (level === "BEGIN") {
    style.color = "#1EC622";
    nlevel = `👉${level}`;
  } else if (level === "PLINE") {
    style.color = "#1EC622";
    nlevel = "👉========================================================================";
  } else if (level === "SINGL") {
    style.color = "#FA8530";
    nlevel = `🌝🌞🌛☀️✨${tag}:`;
  } else {
    style.color = "RGB(0,206,201)";
    nlevel = `💫${level ? level : "VBOSE"}`;
  }
  return { lstyle: style, nlevel };
};

const txtRender = ({ level, tag, str, ltag }) => {
  const { lstyle, nlevel } = getLevelStyle(level, tag);
  const hasTag = level !== "PLINE" && level !== "SINGL";
  return (
    <div className="logger_txt">
      <span style={lstyle}>{nlevel}</span>
      {hasTag ? (
        <span style={{ paddingRight: 10, color: "#6539B9" }}>{`[${
          tag ? tag : "UNDEFINED"
        }]`}</span>
      ) : null}
      {ltag ? <span style={{ color: "#E64386" }}>{`${ltag}. `}</span> : null}
      <span style={{ color: level === "BEGIN" ? "#1EC622" : "white" }}>{str}</span>
    </div>
  );
};

export const logCellRenderer = value => {
  if (typeof value !== "string") {
    return txtRender(value);
  }
  console.log(value);
  return (
    <ReactJson
      name={false}
      theme="monokai"
      iconStyle="circle"
      collapsed={true}
      displayDataTypes={false}
      src={JSON.parse(value)}
      style={{ paddingLeft: 16, backgroundColor: "#ffffff00" }}
    />
  );
};
